.section-contacts {
	position: relative;
	z-index: 0;
	padding-top: 40px;
	padding-bottom: 57px;
	background-color: $color-blue-light3;
	overflow: hidden;

	@media (min-width: $breakpoint-laptop) {
		padding-top: 116px;
		padding-bottom: 64px;
	}

	&__row {
		display: block;

		@media (min-width: $breakpoint-desktop-xl) {
			display: grid;
			grid-template-columns: 1.55fr 1fr;
			grid-column-gap: 90px;
		}
	}

	&__form-box {
		position: relative;
	}

	&__img-box {
		display: none;

		@media (min-width: $breakpoint-desktop-xl) {
			display: block;
		}
	}

	&__img {
		width: 100%;
	}

	&__title {
		margin-top: 0;
		margin-bottom: 73px;
		color: $color-blue;

		@media (max-width: $breakpoint-laptop) {
			margin-bottom: 48px;
			font-size: 32px;
			line-height: 32px;
		}

		.section-contacts--job & {
			display: flex;
			justify-content: space-between;
			align-items: flex-end;
			margin-bottom: 47px;
			font-family: $font-secondary;
			font-size: 50px;
			line-height: 100px;
			font-weight: 700;
			text-align: unset;
		}
	}

	&__form {
		width: 100%;
		max-width: 1060px;
		margin: 0 auto;

		.section-contacts--job & {
			margin: 0;
		}
	}

	&__form-row {
		@media (min-width: $breakpoint-laptop) {
			display: flex;
			margin: 0 -16px 35px;
		}

		.section-contacts--job & {
			margin-bottom: 63px;
			transform: none;
		}

		&--button {
			@media (max-width: $breakpoint-laptop) {
				display: flex;
				flex-direction: column-reverse;
			}
		}
	}

	&__form-col {
		width: 100%;

		@media (min-width: $breakpoint-laptop) {
			padding: 0 16px;
		}

		&--inputs {
			max-width: 100%;

			@media (min-width: $breakpoint-laptop) {
				max-width: 43%;
			}

			.section-contacts--job & {
				max-width: 46%;
			}
		}

		&--message {
			max-width: 100%;

			@media (min-width: $breakpoint-laptop) {
				max-width: 57%;
			}

			.section-contacts--job & {
				max-width: 54%;
			}
		}
	}

	&__form-input {
		display: block;
		width: 100%;
	}

	&__form-textarea {
		display: block;
		width: 100%;
		height: 100%;
		min-height: 139px;
		margin: 23px 0;

		@media (min-width: $breakpoint-laptop) {
			margin: 0;
			min-height: auto;
		}
	}

	&__input-box {
		&:not(:last-child) {
			margin-bottom: 23px;
		}
	}

	&__textarea-box {
		height: 100%;
	}

	&__form-submit-box {
		display: flex;
		align-items: center;
		flex-direction: column;

		@media (min-width: $breakpoint-laptop) {
			flex-direction: row;
		}

		.section-contacts--job & {
			flex-direction: row;
			text-align: unset;
		}
	}

	&__form-submit {
		width: 100%;

		@media (min-width: $breakpoint-laptop) {
			max-width: 255px;
		}
	}

	&__call-text {
		margin-left: 30px;
		font-family: $font-primary;
		font-size: 24px;
		line-height: 100px;
		font-weight: 600;
		color: $color-blue;
	}

	&__call-link {
		color: inherit;

		&:hover {
			text-decoration: underline;
		}
	}

	&__form-upload-text {
		margin-top: 0;
		margin-bottom: 25px;
		font-size: 18px;
		line-height: 18px;
		font-weight: 700;
		color: $color-blue;
	}

	&__form-upload-input {
		display: none;
	}

	&__form-upload-label {
		display: block;
		margin: 0 0 27px;
		padding: 20px 32px;
		border: 1px dashed $color-blue;
		border-radius: 40px;
		font-family: $font-secondary;
		font-size: 21px;
		line-height: 18px;
		font-weight: 700;
		color: $color-blue;
		text-align: center;
		cursor: pointer;

		@media (min-width: $breakpoint-laptop) {
			display: inline-block;
			margin: 0;
			font-family: $font-additional;
			padding: 12px 32px;
			font-size: 18px;
		}
	}

	&__form-layer {
		transition: opacity $transition-default;

		&--form {
			.section-contacts__form--submitted & {
				opacity: 0;
			}
		}

		&--success {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			opacity: 0;
			visibility: hidden;

			@media (max-width: $breakpoint-laptop) {
				text-align: center;
			}

			.section-contacts__form--submitted & {
				visibility: visible;
				opacity: 1;
			}
		}
	}

	&__submitted-icon {
		display: block;
		width: 100px;
		height: 100px;
		margin: 0 auto 45px;

		@media (min-width: $breakpoint-laptop) {
			margin: 0 0 45px;
		}
	}

	&__submitted-title {
		margin-top: 0;
		margin-bottom: 55px;
	}

	&__submitted-text {
		margin-top: 0;
		margin-bottom: 68px;
		font-family: $font-secondary;
		font-size: 24px;
		line-height: 24px;
		font-weight: 700;
	}

	&__form-reset {
		width: 100%;
		max-width: 246px;
	}

	&__error-message {
		display: none;
		font-family: $font-secondary;
		font-size: 18px;
		line-height: 18px;
		color: $color-red;

		@media (min-width: $breakpoint-laptop) {
			margin-left: 33px;
		}

		.section-contacts__form--error & {
			display: block;
		}

		.section-contacts--job & {
			@media (min-width: $breakpoint-laptop) {
				margin-left: 0;
			}
		}
	}
}
